      padding: 0;
      opacity: 0;
      width: 0;
      height: 0; }
      .switch label input[type=checkbox]:checked, .switch label input[type=checkbox]:not(:checked) {
        position: absolute;
        left: -9999px;
        visibility: hidden; }
      .switch label input[type=checkbox]:checked + .lever {
        background-color: #43c4ff; }
        .switch label input[type=checkbox]:checked + .lever:after {
          background-color: #03A9F4;
          left: 24px; }

